.fv-select-action-wrapper{margin-left:10px}.fv-select-action-wrapper button{min-width:120px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border:0;padding-left:10px;padding-right:10px;transition:background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;background-color:white;color:var(--color-default-text);box-shadow:none;min-height:30px;border:1px solid var(--color-grey-dark);border-radius:3px;line-height:25px;align-items:center;outline:none}.fv-select-action-wrapper button:hover{border-color:var(--color-primary)}.fv-select-action-wrapper button .caret:before{content:"\25BC";margin-left:5px;color:var(--color-grey-dark);font-size:0.7em}.fv-select-action-wrapper button .caret:before:hover{color:var(--color-primary)}.fv-select-action-wrapper .fv-action-wrapper{width:120px;height:0;z-index:1;position:absolute;margin-top:2px}.fv-select-action-wrapper .fv-action-wrapper ul{z-index:1;border:1px solid #ccc;border-radius:3px;right:22px;margin-top:1px;list-style:none;padding:4px 0px;background-color:white;box-shadow:0 3px 6px rgba(0,0,0,0.175)}.fv-select-action-wrapper .fv-action-wrapper ul li{display:block;padding:6px 18px;white-space:nowrap}.fv-select-action-wrapper .fv-action-wrapper ul li a{text-decoration:none;display:block;color:black}.fv-select-action-wrapper .fv-action-wrapper ul li:hover{background-color:#f5f5f5;cursor:pointer}
