.fv-chart-selector-wrapper{display:flex;width:90px;justify-content:flex-end}.fv-chart-selector-wrapper>div{display:flex}.fv-chart-selector-wrapper>div .btn{border:none;background:unset;margin:0;outline:none}.fv-chart-selector-wrapper>div .btn:disabled{background:none !important;color:#b7b7b7 !important;cursor:not-allowed}.fv-chart-selector-wrapper>div .active{color:var(--color-primary);cursor:not-allowed !important}.fv-chart-selector-wrapper>div span.dashicons{cursor:pointer;width:30px;height:30px;display:block}.fv-chart-selector-wrapper>div span.dashicons:before{font-size:30px}
